@import url('https://fonts.googleapis.com/css2?family=Caveat+Brush&display=swap');

.todo_wrapper{
    display: grid;
    place-items: center;
    padding: 35px 15px;
    width: 500px;
    gap: 2rem;
    height: auto;
    margin: 0 auto;
    background-color: #d4d9dc;
    overflow-x: hidden;
}
.todo_wrapper div{
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
}
.todo_form{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}
.todo_form input{
    border: none;
    outline: none;
    padding: 10px 20px;
    background-color: #939ea2;
    border-radius: 12px;
    width: 100%;
    color: #ebeef0;
    font-size: 18px;
}
.todo_form button{
    margin-left: 1rem;
    border: none;
    outline: none;
    color: #fff;
    font-size: 18px;
    text-transform: lowercase;
    background-color: #434d55;
    border-radius: 5px;
    padding: 9px 7px;
    cursor: pointer;
    transition: all .4s ease;
}
.todo_form button:hover{
    background-color: #5c6a75;
}

.todo_list p{
    width: 100%;
    font-size: 19px;
    font-weight: 500;
    color: #25371c;
    padding: 5px 15px;
    cursor: pointer;
    text-align: justify;
}
.todo_list .completed{
    text-decoration: line-through;
    align-self: center;
    color: #434d55;
}

.todos div svg{
    color: #8b7b73;
    cursor: pointer;
}
.todos div svg:hover{
    color: #473f3a;
}

.parent{
    display: flex;
    align-items: center;
    justify-content: center;
}
.search{
    flex: 3;
}
.search h3{
    color: #5c6a75;
    font-size: 20px;
}
h3{
    flex: 5;
    font-size: 35px;
    width: 100%;
    text-align: center;
    font-family: "Caveat Brush", cursive;
    font-weight: 300;
    color: #6c7772;
}
.search input{
    border: none;
    width: 100%;
    outline: none;
    background-color: transparent;
}
.search input::placeholder{
    color: #674a3b;
    font-size: 17px;
}

@media screen and (max-width: 510px){
    .todo_wrapper{
        width: 330px;
        margin: 0 auto;
    }
    .todo_form{
        
        width: fit-content;
    }
    .parent{
        flex-direction: column-reverse;
        gap: 1rem;
    }
    .todo_list p{
        font-size: 16px;
        width: 100%;
    }
}